html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
#app {
  height: 100%;
}
#app .header {
  width: 100%;
  position: absolute;
  z-index: 9;
  left: 0;
  top: 0;
}
#app .sc {
  margin-top: 46px;
}
#app .wy-tab {
  height: 44px;
  position: relative;
  width: 1110px;
}
#app .my-scroller {
  top: 90px;
}



